
import { defineComponent, PropType } from 'vue'
import { SKILLS } from '@/consts/const'
import { Skill, SkillType } from '@/types'

interface Data {
	skills: SkillType[]
}

export default defineComponent({
	name: 'AddSkill',
	emits: ['add', 'close'],
	props: {
		existedSkills: {
			type: Array as PropType<Skill[]>,
			default: () => [],
		},
	},
	data(): Data {
		return {
			skills: SKILLS.filter(skill => !this.existedSkills?.find(s => s.name === skill.name)).sort((a, b) =>
				a.name.localeCompare(b.name)
			),
		}
	},
	methods: {
		add(skillName: string) {
			this.$emit('add', {
				name: skillName,
				level: 1,
				experience: 0,
			})
			this.$emit('close')
		},
	},
})
